'use strict';

/**
* Single-precision floating-point NaN.
*
* @module @stdlib/constants/float32/nan
* @type {number}
*
* @example
* import FLOAT32_NAN from '@stdlib/constants/float32/nan';
* // returns NaN
*/


// MAIN //

/**
* Single-precision floating-point NaN.
*
* @constant
* @type {number}
* @default NaN
* @see [IEEE 754]{@link https://en.wikipedia.org/wiki/IEEE_754-1985}
*/
var FLOAT32_NAN = NaN;


// EXPORTS //

export default FLOAT32_NAN;
